export default {
	data() {
		return {
			disabled:true,
			email:'',
			password:'',
			loading:false
		}
	},
	watch:{
		email(val){
			this.change()
		},
		password(val){
			this.change()
		}
	},
	methods: {
		// 监听输入框
		change(){
			if(this.email && this.password){
					this.disabled=false
					return
			}
			this.disabled=true
		},
		// 验证层
		check(){
			if(!this.email&&this.email==''){
				uni.showToast({
					title:'邮箱不能为空',
					icon:'none'
				})
					// 验证邮箱格式
			let ePattern = /^([A-Za-z0-9_\-\.])+\@([A-Za-z0-9_\-\.])+\.([A-Za-z]{2,4})$/;
			if(!ePattern.test(this.email)){
				uni.showToast({
					title: '请输入正确邮箱格式',
					icon:"none"
				});
				return false;
			}
				return false
			}
			if(!this.password&&this.password==''){
				uni.showToast({
					title:'密码不能为空',
					icon:'none'
				})
				return false
			}
			return true
		},
		// 提交更改密码
		submit(){
			this.loading=true
			this.disabled=true
			
			if(!this.check()){this.loading=false;this.disabled=false; return}
			// 提交服务器
			uni.showToast({
				title:'修改成功'
			})
			this.loading=false;
			this.disabled=false;
		}
	}
}
